
import React, {useState, useEffect } from 'react'
import { useHistory, useParams} from 'react-router-dom'
import styled from 'styled-components'
//import './Detail.scss';


/*
    Compont's LifeCycle
        : 등장(최초 랜더링(마운트)) -> 업데이트(재랜더링) -> 퇴장

    HOOK 걸 수 있다

    ex> class 방식 - componentDidMount(){}, componentWillUnmount(){}
    useEffect();
*/


function Detail(props){

    let [alert,setAlert] = useState(true);
    let {id} = useParams();
    let history = useHistory();
    let [input, setInput] = useState('');


    useEffect(()=>{ //여러개 쓰면 순서대로 됨
        //컴포넌트 mount, update 되었을때마다 실행 됨
       let timer = setTimeout(()=>{ setAlert(false); }, 2000);

       return ()=>{ clearTimeout(timer)} //unmount될 떄 실행됨
    }, [alert, input]); 
    /*
    []없으면 컴포넌트 업뎃될때마다 무조건 실행 = 처음로드 + []안에 변수가 변경이 될때만 실행
    empty [] 는 영영 실행이 안된다 (업뎃될 조건이 없으니까) - 최초로드시 한번만 실행
    */

    


    let box = styled.div`
        padding : 20px;
    `;

    let 제목 = styled.h4`
        font-size: 25px;
        color: ${ props => props.색상 }
    `;


    return (
      <div className="container">
          <box>
              <제목 색상='red'>box</제목>
              <제목 className='red'>Detail</제목>
          </box>


            {input}
          <input onChange={(e) => setInput(e.target.value) }/>

            {
            alert === true 
            ? (
                <div className="my-alert-red">
                    <p>재고가 얼마남지 않았습니다!</p>
                </div>
            ) : null
            }
          

          <div className="row">
            <div className="col-md-6">
              <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
            </div>
            <div className="col-md-6 mt-4">
              <h4 className="pt-5">{props.shoes[id].title}</h4>
              <p>{props.shoes[id].content}</p>
              <p>{props.shoes[id].price}원</p>

                <stockInfo stock={props.stock} />

              <button className="btn btn-danger" onClick={()=>{
                  props.setStock(9,11,12)
              }}>주문하기</button> 
              <button className="btn btn-danger" onClick={()=>{ history.goBack(); }}>뒤로가기</button> 
              <button className="btn btn-danger" onClick={()=>{ history.push('/'); }}>메인이동</button> 
            
            </div>
          </div>
      </div>
    )
  }


  function stockInfo(props){
      return (
        <p>재고: {props.stock[0]}</p>
      )
  }
//컴포넌트 많아져, props 지옥 -> contextAPI / redux 탄생

  export default Detail;
